import {Route,Switch,useHistory,useParams} from 'react-router-dom'
import './App.css';
import About from "./About";
import Jay from "./Jay";
import Dhrupal from "./Dhrupal";
import Error from "./Error";
import Bhavin from "./bhavin";
import Namemenu from './Namemenu';
import group from './jay-group.jpg'
import jay from './jay.jpg';
import dhrupal from './dhrupal.jpg';
import bhavin from './bhavin.jpg';


const value = [
  {id:0,val:group, name:"Group",fullname:"our Group"},
  {id:1,val:jay, name:"Jay",fullname:"Jay Karsariya"},
  {id:2,val:dhrupal, name:"Dhrupal",fullname:"Dhrupal Sojitra"},
  {id:3,val:bhavin , name:"Bhavin",fullname:"Bhavin Viradiya"},
];


function App() {
  const history = useHistory(); 
  return (
    <>  
     <div className="menu-div">
      <ul>
        {value.map((item)=>
          <li><p classnName="btn" onClick={()=>history.push(`/Group/${item.id}`)} >{item.name}</p></li>
        )}
      </ul>
  </div>
  { <Route path="/Group/:id" children={<Modal />} />}
    </>
  );
}




function Modal() {
  let history = useHistory();
  let { id } = useParams();
  let image = value[parseInt(id)];

  let back = e => {
    e.stopPropagation();
    history.goBack();
  };

  return (
    <div
      onClick={back}
      style={{
        position: "absolute",
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
        
      }}
    >
      <div
        className="modal"
        style={{
          position: "absolute",
          background: "#fff",
          top: 25,
          left: "10%",
          right: "10%",
          padding: 15,
          border: "2px solid #444"
        }}
      >
        <h1>{image.name}</h1>
        <img src={image.val} className="img"/>
        <h3>This is a pic of {image.fullname}</h3>
        <button  className="close-btn" type="button" onClick={back}>
          Close
        </button>
      </div>
    </div>
  );
}

export default App;
