import React from 'react';
import './App.css';
import {withRouter} from "react-router-dom"

const About = (prop) => {
    console.warn(prop);
    return (
        <>
        <div className="main-div">
            <img src={prop.match.params.val} className="img"/>
            <h1>Friend Forever </h1>
            <h3>{prop.match.params.name}</h3>
        </div>
        </>
    )
};

export default withRouter(About);